/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}


/* HTML5 display-role reset for older browsers */

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
    display: block;
}

body {
    line-height: 1;
}

ol,
ul {
    list-style: none;
}

blockquote,
q {
    quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
    content: '';
    content: none;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

body {
    background-color: #46b646;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.top {
    height: 25vh;
}

.title {
    text-align: center;
    color: rgb(32, 27, 27);
    text-shadow: 2px 2px 4px #FFA500;
    text-decoration: underline;
    font-style: italic;
    font-family: "Pacifico", cursive;
    padding-top: 25px;
    font-size: 100px;
}

.byline {
    text-align: center;
    font-size: 32px;
    font-family: cursive;
    margin-top: 40px;
}

footer {
    display: flex;
    background-color: #000;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    font-family: cursive;
    font-size: 40px;
    padding: 20px 0;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    color: #fff;
    justify-content: space-between;
    height: 5vh;
}

.left {
    display: flex;
    border: none;
    padding-left: 15px;
    font-size: 18px;
    color: #90EE90;
}

.right {
    padding-right: 15px;
}

img {
    width: 160px;
    margin: auto;
    margin-left: auto;
}

.pic {
    height: 50px;
    width: 50px;
    margin-right: auto;
}

#hackFooter {
    margin: auto;
    margin-right: 10px;
    font-size: 25px;
    color: #fba;
}

